import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  Select,
  Radio,
  Divider,
  Col,
  InputNumber,
  Checkbox
} from "antd";
import getLanguage from "../../../App/MyLanguange";
import ModalAddAssetFiscal from "./modalAddAssetFiscal";
import ModalAddAssetCategory from "./modalAddAssetCategory";

const { Option } = Select;
const InputGroup = Input.Group;

class AssetTypeCreateForm extends Component {
  state = {
    check_code: false
  };

  // Rumuse Depreciation Rate Boy
  depreciationRate = year => {
    let rate = 100 / year;

    return rate;
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["asset_type_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  handleChangeAssetFiscal = id_asset_fiscal => {
    this.props.assetType.asset_fiscals.map(data => {
      if (data.id_asset_fiscal === id_asset_fiscal) {
        const { setFieldsValue } = this.props.form;

        setFieldsValue({
          ["depreciation_method"]: data.depreciation_method,
          ["estimated_life_year"]: data.estimated_life_year,
          ["depreciation_rate"]: data.depreciation_rate
        });
      }
    });
  };

  handleChangeFromDepreciationMethod = e => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    let value = e.target.value;
    let depreciation_rate = this.depreciationRate(
      getFieldValue("estimated_life_year")
    );

    if (value === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (value === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (value === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }
  };

  handleChangeFromEstimatedLifeYear = value => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    let depreciation_method = getFieldValue("depreciation_method");
    let depreciation_rate = this.depreciationRate(value);

    if (depreciation_method === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (depreciation_method === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (depreciation_method === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }
  };

  render() {
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { assetType } = this.props;
    const { loading } = assetType;
    const error = assetType.errorField ? assetType.errorField : [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <>
        <ModalAddAssetCategory
          handleModal={this.props.handleModalAddAssetCategory}
          assetCategory={this.props.assetCategory}
          assetType={this.props.assetType}
          submit={this.props.handleSubmitAssetCategory}
          visible={this.props.visibleAddAssetCategory}
        />
        <ModalAddAssetFiscal
          handleModal={this.props.handleModalAddAssetFiscal}
          assetFiscal={this.props.assetFiscal}
          submit={this.props.handleSubmitAssetFiscal}
          visible={this.props.visibleAddAssetFiscal}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetCategories.pageHeader" />}
              >
                {getFieldDecorator("id_asset_category", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {permissions.includes("Create Asset Category") ? (
                      <Option
                        key={"Create Asset Category"}
                        value={undefined}
                        onClick={() => this.props.handleModalAddAssetCategory()}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "assetCategories.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}

                    {this.props.assetType.asset_categories.map((data, key) => (
                      <Option key={key} value={data.id_asset_category}>
                        {data.asset_category_code +
                          " (" +
                          data.asset_category_name +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="assetTypes.add.code" />}
                    validateStatus={
                      error.asset_type_code || getFieldError("asset_type_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.asset_type_code
                        ? error.asset_type_code
                        : getFieldError("asset_type_code")
                    }
                  >
                    {getFieldDecorator("asset_type_code", {
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetTypes.add.name" />}
              >
                {getFieldDecorator("asset_type_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetFiscals.pageHeader" />}
              >
                {getFieldDecorator("id_asset_fiscal", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.assetType.loading}
                    showSearch
                    optionFilterProp="children"
                    onChange={this.handleChangeAssetFiscal}
                  >
                    {permissions.includes("Create Asset Fiscal") ? (
                      <Option
                        key={"Create Asset Fiscal"}
                        value={undefined}
                        onClick={() => this.props.handleModalAddAssetFiscal()}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "assetFiscals.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}

                    {this.props.assetType.asset_fiscals.map((data, key) => (
                      <Option key={key} value={data.id_asset_fiscal}>
                        {data.asset_fiscal_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={
                  <IntlMessages id="assetFiscals.add.depreciationMethod" />
                }
              >
                {getFieldDecorator("depreciation_method", {
                  initialValue: 2,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group
                    style={{ display: "flex" }}
                    buttonStyle="solid"
                    onChange={this.handleChangeFromDepreciationMethod}
                    loading={this.loadingAssetFiscal}
                  >
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={1}
                    >
                      <IntlMessages id="Non Depreciable" />
                    </Radio.Button>
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={2}
                    >
                      <IntlMessages id="Straight Line" />
                    </Radio.Button>
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={3}
                    >
                      <IntlMessages id="Double Declining Method" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="assetFiscals.add.estimatedLifeYear" />
                    }
                  >
                    {getFieldDecorator("estimated_life_year", {
                      initialValue: 1,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        precision={0}
                        min={getFieldValue("depreciation_method") === 3 ? 3 : 1}
                        onChange={this.handleChangeFromEstimatedLifeYear}
                        loading={this.loadingAssetFiscal}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="assetFiscals.add.depreciationRate" />
                    }
                  >
                    {getFieldDecorator("depreciation_rate", {
                      initialValue: 100,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        disabled={true}
                        loading={loading}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </div>
          </div>

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="assetTypes.add.subtitle.account" />
          </Divider>

          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetTypes.add.assetAccount" />}
              >
                {getFieldDecorator("id_account_asset", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.assetType.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {this.props.assetType.accounts.map((data, key) =>
                      data.id_account_type === "06" ? (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ) : null
                    )}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetTypes.add.depreciationAccount" />}
              >
                {getFieldDecorator("id_account_depreciation", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.assetType.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {this.props.assetType.accounts.map((data, key) =>
                      data.id_account_type === "06" ? (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ) : null
                    )}
                  </Select>
                )}
              </Form.Item>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetTypes.add.expenseAccount" />}
              >
                {getFieldDecorator("id_account_expense", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.assetType.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {this.props.assetType.accounts.map((data, key) =>
                      data.id_account_type === "15" ||
                      data.id_account_type === "16" ? (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ) : null
                    )}
                  </Select>
                )}
              </Form.Item>
            </div>
          </div>

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "20px", marginLeft: "50px" }}
              loading={loading}
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedAssetTypeCreateForm = Form.create({ name: "create_asset_type" })(
  AssetTypeCreateForm
);

export default WrappedAssetTypeCreateForm;
