import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Radio, Modal, Row, InputNumber } from "antd";

class ModalAddAssetFiscal extends Component {
  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.assetFiscal;
    if (success) {
      resetFields();
    }
  };

  // Rumuse Depreciation Rate Boy
  depreciationRate = year => {
    let rate = 100 / year;

    return rate;
  };

  handleChangeFromDepreciationMethod = e => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    let value = e.target.value;
    let depreciation_rate = this.depreciationRate(
      getFieldValue("estimated_life_year")
    );

    if (value === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (value === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (value === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }
  };

  handleChangeFromEstimatedLifeYear = value => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    let depreciation_method = getFieldValue("depreciation_method");
    let depreciation_rate = this.depreciationRate(value);

    if (depreciation_method === 1) {
      setFieldsValue({
        depreciation_rate: 0
      });
    } else if (depreciation_method === 2) {
      setFieldsValue({
        depreciation_rate: depreciation_rate
      });
    } else if (depreciation_method === 3) {
      setFieldsValue({
        depreciation_rate: depreciation_rate * 2
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { assetFiscal } = this.props;
    const { loading } = assetFiscal;
    const error = assetFiscal.errorField ? assetFiscal.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="assetFiscals.button.add" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="50%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetFiscals.add.name" />}
                validateStatus={
                  error.asset_fiscal_name || getFieldError("asset_fiscal_name")
                    ? "error"
                    : null
                }
                help={
                  error.asset_fiscal_name
                    ? error.asset_fiscal_name
                    : getFieldError("asset_fiscal_name")
                }
              >
                {getFieldDecorator("asset_fiscal_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={
                  <IntlMessages id="assetFiscals.add.depreciationMethod" />
                }
              >
                {getFieldDecorator("depreciation_method", {
                  initialValue: 2,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group
                    style={{ display: "flex" }}
                    buttonStyle="solid"
                    onChange={this.handleChangeFromDepreciationMethod}
                  >
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={1}
                    >
                      <IntlMessages id="Non Depreciable" />
                    </Radio.Button>
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={2}
                    >
                      <IntlMessages id="Straight Line" />
                    </Radio.Button>
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={3}
                    >
                      <IntlMessages id="Double Declining Method" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              <Row>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="assetFiscals.add.estimatedLifeYear" />
                    }
                  >
                    {getFieldDecorator("estimated_life_year", {
                      initialValue: 1,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        precision={0}
                        min={getFieldValue("depreciation_method") === 3 ? 3 : 1}
                        onChange={this.handleChangeFromEstimatedLifeYear}
                        style={{ width: "95%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="assetFiscals.add.depreciationRate" />
                    }
                  >
                    {getFieldDecorator("depreciation_rate", {
                      initialValue: 100,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        disabled={true}
                        loading={loading}
                        style={{ width: "100%" }}
                      />
                    )}
                  </Form.Item>
                </Col>
              </Row>
            </div>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddAssetFiscal = Form.create({
  name: "modal_add_asset_fiscal"
})(ModalAddAssetFiscal);

export default WrappedModalAddAssetFiscal;
