import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Modal, Select, Row } from "antd";

const { Option } = Select;

class ModalAddAssetCategory extends Component {
  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.assetCategory;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { assetCategory } = this.props;
    const loading = assetCategory.loadingSubmit;
    const error = assetCategory.errorField ? assetCategory.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="assetCategories.add.subtitle.data" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              {/* <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetCategories.parent" />}
              >
                {getFieldDecorator("id_asset_category_parent")(
                  <Select
                    loading={loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {this.props.assetType.asset_categories.map((data, key) => (
                      <Option key={key} value={data.id_asset_category}>
                        {data.asset_category_code +
                          " (" +
                          data.asset_category_name +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item> */}

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetCategories.code" />}
                validateStatus={
                  error.asset_category_code ||
                  getFieldError("asset_category_code")
                    ? "error"
                    : null
                }
                help={
                  error.asset_category_code
                    ? error.asset_category_code
                    : getFieldError("asset_category_code")
                }
              >
                {getFieldDecorator("asset_category_code", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetCategories.name" />}
              >
                {getFieldDecorator("asset_category_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddAssetCategory = Form.create({
  name: "modal_add_asset_category"
})(ModalAddAssetCategory);

export default WrappedModalAddAssetCategory;
