import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import AssetTypeCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetType/actions";
import assetFiscalActions from "../../../../redux/asset/assetFiscal/actions";
import assetCategoryActions from "../../../../redux/asset/assetCategory/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/asset-types";
const { storeAssetFiscal } = assetFiscalActions;
const { storeAssetCategory } = assetCategoryActions;

class CreateAssetType extends Component {
  state = {
    visibleAddAssetCategory: false,
    visibleAddAssetFiscal: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createAssetType } = this.props;
    createAssetType();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetType.error) {
      notification("error", this.props.assetType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetType.success) {
      this.props.history.replace(redirectTo);
    }

    // Asset Category
    if (this.props.assetCategory.error) {
      notification("error", this.props.assetCategory.error);
      this.props.clearError();
    }

    if (this.props.assetCategory.success) {
      const { createAssetType, clearSuccess } = this.props;
      notification("success", this.props.assetCategory.success);
      this.handleModalAddAssetCategory();
      clearSuccess();
      createAssetType();
    }

    // Asset Fiscal
    if (this.props.assetFiscal.error) {
      notification("error", this.props.assetFiscal.error);
      this.props.clearError();
    }

    if (this.props.assetFiscal.success) {
      const { createAssetType, clearSuccess } = this.props;
      notification("success", this.props.assetFiscal.success);
      this.handleModalAddAssetFiscal();
      clearSuccess();
      createAssetType();
    }
  }

  handleModalAddAssetCategory = () => {
    this.setState({
      visibleAddAssetCategory: !this.state.visibleAddAssetCategory
    });
  };

  handleModalAddAssetFiscal = () => {
    this.setState({
      visibleAddAssetFiscal: !this.state.visibleAddAssetFiscal
    });
  };

  handleSubmit = value => {
    const { storeAssetType } = this.props;
    storeAssetType(value);
  };

  handleSubmitAssetCategory = value => {
    const { storeAssetCategory } = this.props;
    storeAssetCategory(value);
  };

  handleSubmitAssetFiscal = value => {
    const { storeAssetFiscal } = this.props;
    storeAssetFiscal(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <AssetTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            assetType={this.props.assetType}
            // Asset Category
            assetCategory={this.props.assetCategory}
            visibleAddAssetCategory={this.state.visibleAddAssetCategory}
            handleModalAddAssetCategory={this.handleModalAddAssetCategory}
            handleSubmitAssetCategory={this.handleSubmitAssetCategory}
            // Asset Fiscal
            assetFiscal={this.props.assetFiscal}
            visibleAddAssetFiscal={this.state.visibleAddAssetFiscal}
            handleModalAddAssetFiscal={this.handleModalAddAssetFiscal}
            handleSubmitAssetFiscal={this.handleSubmitAssetFiscal}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    assetType: state.assetType,
    assetCategory: state.assetCategory,
    assetFiscal: state.assetFiscal
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeAssetFiscal, storeAssetCategory })
)(CreateAssetType);
